<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <b>班级名称：</b>
        <el-input
          v-model="queryParam.name"
          placeholder="请输入班级名称"
          style="width: 150px"
        ></el-input>
        <el-button
          type="primary"
          @click="
            pageNo = 1;
            pages();
          "
          icon="el-icon-search"
          >搜索</el-button
        >
        <el-button type="primary" @click="reset" icon="el-icon-magic-stick">重置</el-button>
      </el-row>

      <template>
        <el-table :data="tableData" border style="width: 100%" max-height="500">
          <el-table-column type="index" width="55" />
          <el-table-column prop="id" label="ID" width="100" />
          <el-table-column prop="name" label="班级名称" width="100" />
          <el-table-column prop="number" label="班级人数" width="80" />
          <el-table-column prop="headmaster" label="班主任姓名" width="80" />
          <el-table-column prop="email" label="班主任邮箱" />
          <el-table-column prop="telephone" label="班主任电话" />
          <el-table-column prop="gradeName" label="所属年级" />
          <el-table-column prop="introducation" label="班级介绍" />
          <el-table-column label="操作" fixed="right" width="130">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleView(scope.row)">查看学生名单</el-button>
            </template>
          </el-table-column>
        </el-table>

        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pagesizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div> </template
    ></el-card>
    <ClassListDetail ref="ClassListDetail" />
  </div>
</template>
</el-card>

    
  </div>
</template>

<script>
import API from "../../utils/service";
import ClassListDetail from "./ClassListDetail";
export default {
  components: {
    ClassListDetail
  },
  data() {
    return {
      name: '班级名单',
      tableData: [],
      queryParam: {
        name: undefined
      },
      currentPage: 1,
      total: 0,
      pagesize: 5,
      pagesizes: [3, 4, 5, 6]
    };
  },
  mounted() {
    this.$bus.$emit('receiveParams',this.name)
    this.pages(1, this.pagesize);
  },
  methods: {
    handleCurrentChange(val) {
      this.currentPage = val;
      this.pages(val, this.pagesize);
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.pages(this.currentPage, this.pagesize);
    },
    pages() {
      API({
        url: `/clazzController/getClazzsByOpr/${this.currentPage}/${this.pagesize}`,
        method: "get",
        params: {
          ...this.queryParam
        }
      }).then((response) => {
        // this.queryParam.name = ''
        this.total = response.data.data.total;
        this.tableData = response.data.data.records;
      });
    },
    reset() {
      this.queryParam = {};
      this.pageNo = 1;
      this.pages();
    },
    handleView: function(row) {
      this.$refs.ClassListDetail.handleOpen(row);
    }
  }
};
</script>

<style lang="scss" scoped>
.el-row {
  margin: 30px 0 50px 0;
}
.text {
  font-size: 20px;
}

.box-card {
  width: 100%;
}

.el-card {
  padding: 0px;
}

.el-button--primary {
  background-color: #fab6b6;
  border-color: #fab6b6;
}

.el-button--primary:focus,
.el-button--primary:hover {
  background: #e9baba;
  border-color: #f3dfdf;
  color: #fff;
}

::v-deep .el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #f3dfdf;
}
</style>