<template>
  <el-dialog title="学生名单" :visible.sync="visible" width="80%">
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      max-height="500"
    >
      <el-table-column type="selection" width="55" />
      <el-table-column prop="id" label="ID" width="100" />
      <el-table-column prop="clazzName" label="所属班级" width="100" />
      <el-table-column prop="name" label="姓名" width="100" />
      <el-table-column prop="sno" label="学号" />
      <el-table-column prop="gender" label="性别" />
      <el-table-column prop="email" label="邮箱" />
      <el-table-column prop="telephone" label="电话" />
      <el-table-column prop="address" label="住址" />
      <el-table-column prop="introducation" label="介绍" />
    </el-table>
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="pagesizes"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
  </el-dialog>
</template>

<script>
import API from "../../utils/service";
export default {
  data() {
    return {
      visible: false,
      tableData: [],
      currentPage: 1,
      total: 0,
      pagesize: 5,
      pagesizes: [3, 4, 5, 6],
      clazzName: undefined
    };
  },
  methods: {
    handleOpen(item) {
      this.clazzName = item.name;
      this.visible = true;
      this.pages();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.pages(val, this.pagesize);
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.pages(this.currentPage, this.pagesize);
    },
    pages() {
      API({
        url: `/studentController/getStudentByOpr/${this.currentPage}/${this.pagesize}`,
        method: "get",
        params: {
          clazzName: this.clazzName
        }
      }).then((response) => {
        this.total = response.data.data.total;
        this.tableData = response.data.data.records;
      });
    }
  }
};
</script>
